body{
    margin: 0;
    padding: 0;
    /* background: url(./bg.jpg); */
    background-size: cover;
    font-family: 'Comfortaa', cursive;
}
.neon{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    padding: 0 20px;
    font-size: 2em;
    color: #fff;
    text-shadow: 0 0 20px #ff0000;
}
.neon::after{
    content: 'aMessenger';
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 20px;
    z-index: -1;
    color: #ff0000;
    filter: blur(15px);
}
.neon::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fe3a3a;
    z-index: -2;
    opacity: .5;
    filter: blur(40px);
}


